import React from 'react';
import { Container, Row, Col, Button } from "react-bootstrap";
import Clock from "./clock";

interface Task 
{
    right: boolean,
    date: Date
}

interface IState 
{
    hours: number,
    minutes: number,
    tasks: Task[],
    count: number
}

export default class GameTypeOneComponent extends React.Component<{}, IState>
{

    public readonly state: Readonly<IState> = 
    {
        hours: 0,
        minutes: 0,
        tasks: [], 
        count: 3
    }

    public componentDidMount()
    {
        this.newTask();
    }

    private newTask()
    {
        let hours = Math.floor(Math.random() * 24) + 1;
        let minutes = Math.floor(Math.random() * 59) + 1;
        let tasks : Task[] = [];
        let rightIndex = Math.floor(Math.random() * 3);
        for (let i = 0; i < this.state.count; i++)
        {
            if (i == rightIndex) 
            {
                let date = new Date();
                date.setHours(hours, minutes, 0);
                let task : Task = 
                {
                    right: true,
                    date: date
                }
                tasks.push(task);
            }
            else 
            {
                let date = new Date();
                date.setHours(Math.floor(Math.random() * 24) + 1, Math.floor(Math.random() * 59) + 1, 0);
                let task : Task = 
                {
                    right: false,
                    date: date
                }
                tasks.push(task);
            }
            this.setState({
                hours: hours,
                minutes: minutes,
                tasks: tasks
            });
        }
    }

    private answer(answer: boolean)
    {
        if (answer)
            this.newTask();
    }

    public render()
    {
        return (
            <Container>
                <Row>
                    { this.state.tasks.map((task, index) => 
                    (
                        <Col xs={4} className="d-flex flex-column justify-content-center align-items-center">
                            <Clock size={ 200 } time={ task.date } isActive={ false }/>
                            <Button variant="outline-primary" onClick={ () => this.answer(task.right)}>Выбрать</Button>
                        </Col>
                    )) }
                </Row>
                <Row className="d-flex justify-content-center align-items-center">
                    <h1 className="display-1 mt-5">
                        { 
                            (this.state.hours > 9 ? this.state.hours.toString() : "0" + this.state.hours.toString())
                            + ":" + 
                            (this.state.minutes > 9 ? this.state.minutes.toString() : "0" + this.state.minutes.toString())
                        }
                    </h1>
                </Row>
            </Container>
        )
    }   
}
